.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-container {
  display: block; /* Change to block to allow natural document flow */
  background-image: url('/public/images/cars1.webp');
  background-size: cover;
  cursor: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><circle cx="12" cy="12" r="10" fill="black" stroke="white" stroke-width="2"/><circle cx="12" cy="12" r="6" fill="white"/></svg>'), auto;
  overflow-y: auto; /* Keep for scrolling */
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Use min-height to ensure form is centered vertically but allows expansion */
}


@font-face {
  font-family: 'Orbitron';
  src: url('../public/fonts/Insolata.ttf') format('truetype');
}

.customGreenButton {
  background-color: #a58c75 !important; /* Slightly green color */
  color: rgb(40, 50, 82) !important;
}

.customGreenButton:hover {
  background-color: #70ac70 !important; /* A bit darker for hover effect */
}

.custom-typography {
  font-family: 'Orbitron'; 
  font-weight: bold;
  font-size: 2.5rem;
  font-stretch: expanded;
  white-space: nowrap; 
  display: flex;
  justify-content: center;
  align-items: center; 
}
